<template>
  <div class="whoask">
    <div class="container">
      <div class="character-display">
        <img :src="currentCharacter.imageUrl" :alt="currentCharacter.name">
      </div>
      <div class="character-info">
        <div class="character-list-container">
          <div class="character-list" ref="characterList">
            <img v-for="char in characters" :key="char.id" :src="char.imageUrl" :alt="char.name"
              @click="loadCharacterDetails(char)" />
          </div>
          <button class="slider-btn prev" @click="prevSlide">[aa]</button>
          <button class="slider-btn next" @click="nextSlide">[aa]</button>
        </div>
        <div class="character-details">
          <h2>{{ currentCharacter.name }}</h2>
          <p>{{ currentCharacter.attributes }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import charactersData from '@/assets/database.json';

export default {
  data() {
    return {
      characters: [],
      currentCharacter: {},
      currentIndex: 0,
      visibleCharacters: 4,
    };
  },
  mounted() {
    this.characters = charactersData;
    this.loadCharacterDetails(this.characters[0]);
  },
  methods: {
    loadCharacterDetails(character) {
      this.currentCharacter = character;
    },
    updateSliderPosition() {
      const offset = -this.currentIndex * 90;
      this.$refs.characterList.style.transform = `translateX(${offset}px)`;
    },
    prevSlide() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
        this.updateSliderPosition();
      }
    },
    nextSlide() {
      if (this.currentIndex < this.characters.length - this.visibleCharacters) {
        this.currentIndex++;
        this.updateSliderPosition();
      }
    },
  },
};
</script>

